import React, { useEffect } from 'react';
import './header.css';
import { useState } from 'react';
import Group from '../../assets/Group 81.png';
import Head from "../../assets/Illustration.png";
import Glow from '../../assets/Illustration-glow.svg';
import Active from '../../assets/Illustration-active.png';

const Header = () => {
  const [ isActive, setIsActive ] = useState(0);

  useEffect(() => {
    const slideInterval = setInterval(() => {
        setIsActive((state) => (state + 1) % 3);
    }, 2000);

    return () => clearInterval(slideInterval);

}, []);

  return (
    <div className='gpt3__header section_padding' id='home'>

      <div className='gpt3__header-content split'>
        <h1 className="gradient__text">Let's Build Something Amazing With GPT-3 OpenAI</h1>
        <p>Yet bed any for travelling assistance indulgence unpleasing. Not thoughts all exercise blessing. Indulgence way everything joy alteration boisterous the attachment. Party we years to order allow asked of.</p>
        <div className='gpt3__header-content_input'>
          <input type="email" placeholder='Your Email Adress'></input>
          <button type='button'>Get Started</button>
        </div>
        <div className='gpt3__header-content_people'>
          <img src={Group} alt="photos of some fraction of our clients"/>
          <p>1,600 people requested access a visit in last 24 hours</p>
        </div>
      </div>

      <div className='gpt3__header-content_img split'
        onMouseEnter={() => setIsActive(true)}
        onMouseLeave={() => setIsActive(false)}
      >
        {isActive === 0
          ?
          <img className='fade-in' src={Active} alt="gpt3 illustration active"/>
          :
          isActive === 1
          ?
          <img className='fade-out' src={Active} alt="gpt3 illustration active"/>
          :
          isActive === 2
          ?
          <img className='fade-in' src={Head} alt="gpt3 illustration"/>
          :
          isActive === 3
          ?
          <img className='fade-out' src={Head} alt="gpt3 illustration"/>
          :
          isActive === 4
          ?
          <img className='fade-in' src={Glow} alt="gpt3 illustration glow"/>
          :
          <img className='fade-out' src={Glow} alt="gpt3 illustration glow"/>
        }
      </div>

    </div>
  );
};

export default Header;